.product-detail-viewer {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

// ===== 섹션 =====
.viewer-section {
  margin-bottom: 40px;

  &:last-child {
    margin-bottom: 0;
  }
}

// ===== 로우 (가로 배치) =====
.viewer-row {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: 768px) {
    flex-direction: column;
    gap: 15px;
  }
}

// ===== 컬럼 =====
.viewer-column {
  flex: 1;
  min-width: 0; // Flexbox 텍스트 overflow 방지

  @media (max-width: 768px) {
    flex: 1 1 100% !important;
  }
}

// ===== 텍스트 위젯 =====
.widget-text {
  line-height: 1.6;
  word-break: break-word;
  white-space: pre-wrap;

  // 기본 타이포그래피
  h1, h2, h3, h4, h5, h6 {
    margin: 1em 0 0.5em;
    font-weight: bold;
    line-height: 1.3;
  }

  h1 { font-size: 2em; }
  h2 { font-size: 1.75em; }
  h3 { font-size: 1.5em; }
  h4 { font-size: 1.25em; }
  h5 { font-size: 1.1em; }
  h6 { font-size: 1em; }

  p {
    margin: 0.75em 0;
  }

  a {
    color: #1976d2;
    text-decoration: underline;

    &:hover {
      color: #1565c0;
    }
  }

  ul, ol {
    margin: 1em 0;
    padding-left: 2em;
  }

  blockquote {
    margin: 1em 0;
    padding: 10px 20px;
    border-left: 4px solid #e0e0e0;
    background: #f5f5f5;
    font-style: italic;
  }

  code {
    padding: 2px 6px;
    background: #f5f5f5;
    border-radius: 4px;
    font-family: 'Courier New', monospace;
    font-size: 0.9em;
  }

  pre {
    margin: 1em 0;
    padding: 15px;
    background: #f5f5f5;
    border-radius: 8px;
    overflow-x: auto;

    code {
      padding: 0;
      background: none;
    }
  }
}

// ===== 이미지 위젯 =====
.widget-image-container {
  width: 100%;
  text-align: center;
}

.widget-image {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.image-description {
  margin-top: 10px;
  font-size: 14px;
  color: #666;
  text-align: center;
  font-style: italic;
}

// ===== Embed 위젯 =====
.widget-embed-container {
  width: 100%;
  position: relative;
  padding-bottom: 56.25%; // 16:9 비율
  height: 0;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.widget-embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

// ===== 데이터 없음 =====
.no-detail {
  padding: 60px 20px;
  text-align: center;
  color: #999;
  font-size: 16px;
}

// ===== 반응형 =====
@media (max-width: 768px) {
  .product-detail-viewer {
    padding: 15px;
  }

  .viewer-section {
    margin-bottom: 30px;
  }

  .widget-text {
    font-size: 14px;

    h1 { font-size: 1.75em; }
    h2 { font-size: 1.5em; }
    h3 { font-size: 1.25em; }
  }
}
